import { GET_ONE_ITEM } from "../actionTypes/details";
import { catchError } from "../actions/error";
import axios from "axios";
export const oneItem = item => ({
  type: GET_ONE_ITEM,
  payload: item
});

export function getOneItem(id) {
  let status;
  return dispatch => {
    axios
      .get(`http://localhost:3010/api/discs/${id}`)
      .then(response => {
        status = response.status;
        return response.data;
      })
      .then(data => {
        if (status >= 400) catchError(dispatch, status);
        dispatch(oneItem(data));
      })
      .catch(err => dispatch(catchError(err)));
  };
}
