import React, { Component } from "react";
import { Redirect } from "react-router-dom";

class Error extends Component {
  componentWillUnmount() {
    this.props.cleanError();
  }

  render() {
    if (this.props.error) {
      return <Redirect to="/notfound" />;
    }

    return null;
  }
}

export default Error;
