import { GET_ITEMS } from "../actionTypes/discs";

const INITIAL_STATE = { products: [], page: 1 };

export default function discReducer(state = INITIAL_STATE, action) {
  const { type, payload } = action;
  switch (type) {
    case GET_ITEMS:
      return {
        ...state,
        products: [...state.products, ...payload.items],
        canLoadMore: payload.canLoadMore,
        page: state.page + 1
      };
    default:
      return state;
  }
}
