import React, { Component } from "react";
import { Link } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import CardActionArea from "@material-ui/core/CardActionArea";
import Typography from "@material-ui/core/Typography";

const styles = {
  card: {
    width: 240,
    margin: "10px 20px"
  },
  media: {
    height: 140
  },
  link: {
    textDecoration: "none"
  }
};

class ProductItem extends Component {
  render() {
    const { id, name, singer, price } = this.props.product;
    const { classes, addToCart } = this.props;
    return (
      <Card className={classes.card}>
        <Link to={`/product/${id}`} className={classes.link}>
          <CardActionArea>
            <CardMedia
              className={classes.media}
              image={`http://lorempixel.com/800/800/nature/${id}`}
              title="Contemplative Reptile"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                {name}
              </Typography>
              <Typography component="p">{singer}</Typography>
            </CardContent>
          </CardActionArea>
        </Link>

        <CardActions>
          <Button
            size="small"
            color="primary"
            onClick={() => addToCart(this.props.product)}
          >
            Add to card
          </Button>
          <Button size="small" color="primary">
            {price} $
          </Button>
        </CardActions>
      </Card>
    );
  }
}

export default withStyles(styles)(ProductItem);
