import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

const styles = {
  card: {
    width: 800,
    height: 800,
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between"
  },
  media: {
    height: 400
  },
  container: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    paddingTop: "30px"
  },
  btns: {
    display: "flex",
    width: "100%",
    justifyContent: "space-between"
  },
  addToCart: {
    fontSize: "20px"
  }
};

class ProductPage extends Component {
  componentDidMount() {
    if (Object.keys(this.props.disc).length === 0)
      this.props.getOneItem(this.props.match.params.id);
  }

  render() {
    const { classes, addToCart } = this.props;
    const { id, name, singer, price, description } = this.props.disc;

    return (
      <div className={classes.container}>
        <Card className={classes.card}>
          <div>
            <CardMedia
              className={classes.media}
              image={`http://lorempixel.com/800/800/nature/${id}`}
              title={name}
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                Album: {name}
              </Typography>
              <Typography gutterBottom variant="h6" component="h2">
                Singer: {singer}
              </Typography>
              <Typography component="p">{description}</Typography>
            </CardContent>
          </div>
          <CardActions className={classes.btns}>
            <Button
              size="small"
              color="primary"
              className={classes.addToCart}
              onClick={() => addToCart(this.props.disc)}
            >
              Add to card
            </Button>
            <Button size="small" color="primary" className={classes.addToCart}>
              {price} $
            </Button>
          </CardActions>
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(ProductPage);
