import { GET_ITEMS } from "../actionTypes/discs";
import axios from "axios";

export const getItems = (items, canLoadMore) => ({
  type: GET_ITEMS,
  payload: { items, canLoadMore }
});

export function fetchData(page) {
  let canLoadMore;
  return (dispatch, getState) => {
    axios
      .get(`http://localhost:3010/api/discs?_page=${page}&_limit=6`)
      .then(response => {
        canLoadMore =
          Number(response.headers["x-total-count"]) -
            getState().discs.products.length <
          6
            ? true
            : false;
        return response.data;
      })
      .then(data => dispatch(getItems(data, canLoadMore)));
  };
}
